.updateProfileContainer {
  width: 100vw;
  height: 90vh;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
}
.updateProfileBox {
  width: 30%;
  height: 70%;
  background-color: rgba(255, 255, 255, 0.507);
  box-sizing: border-box;
  overflow: hidden;
}

.updateProfileBox div button {
  background-color: black;
  height: 3px;
  width: 50%;
  border: none;
  transition: all 0.5s;
}
.updateProfileHeading {
  text-align: center;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1.3vmax NikeTg;
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.267);
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.updateProfileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.updateProfileForm div {
  display: flex;
  width: 100%;
  align-items: center;
}

.updateProfileForm div input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax Helvetica;
  outline: none;
}

.updateProfileForm div svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.651);
}

#updateProfileImage img {
  width: 3vmax;
  border-radius: 100%;
  margin-right: 1vmax;
}
#updateProfileImage input {
  display: flex;
  padding: 0;
}
#updateProfileImage input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0;
  font: 400 0.8vmax Helvetica;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.651);
  background-color: rgb(231, 231, 231);
}

.updateProfileBtn {
  border: none;
  background-color: black;
  color: white;
  font: 300 0.9vmax NikeTg;
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.267);
}

.updateProfileBtn:hover {
  background-color: rgb(61, 61, 61);
}

@media screen and (max-width: 700px) {
  .updateProfileBox {
    width: 70%;
    height: 40%;
  }
}
